.container {
    width: min(100% - 30px, 1280px);
    margin-inline: auto;
}
.child {
    display: grid;
    place-content: center;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
}

h2{
    font-size: 38px;
    color: darkgray;
}
input{
    height: 40px;
    width: 280px;
    border-radius: 4px;
    margin-right: 20px;
    border: 1px solid #fff;

}

button{
    height: 40px;
    width:100px;
    border-radius: 4px;
    border: 1px solid cadetblue;
    background-color: cadetblue;

}

button:hover{
    background-color: darkcyan;
    border: 1px solid darkcyan;
    

}

p{
    color:#fff
}